import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  INCREASE_PRODUCT_QUANTITY,
  DECREASE_PRODUCT_QUANTITY,
} from './actionTypes';

const addProduct = product => ({
  type: ADD_PRODUCT,
  payload: product,
});

const removeProduct = product => ({
  type: REMOVE_PRODUCT,
  payload: product,
});

const decreaseProductQuantity = (product, index) => ({
  type: DECREASE_PRODUCT_QUANTITY,
  payload: {
    index,
    product,
  },
});

const increaseProductQuantity = (product, index) => ({
  type: INCREASE_PRODUCT_QUANTITY,
  payload: {
    index,
    product,
  },
});

export {
  addProduct,
  removeProduct,
  decreaseProductQuantity,
  increaseProductQuantity,
};
